import Joi from 'joi';

function validateUserSignUpDetails(userDetails) {
  const schema = {
    firstName: Joi.string()
      .regex(/^[a-zA-Z]+$/)
      .required()
      .error(new Error('firstName is invalid')),

    lastName: Joi.string()
      .regex(/^[a-zA-Z]+$/)
      .required()
      .error(new Error('lastName is invalid')),

    email: Joi.string().trim().email()
      .regex(/^[A-Za-z0-9_]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/)
      .required()
      .error(new Error('email is invalid')),

    password: Joi.string().trim().min(6).required(),
  };

  return Joi.validate(userDetails, schema);
}

function validateUserSignInDetails(userDetails) {
  const schema = {
    email: Joi.string().email()
      .regex(/^[A-Za-z0-9_]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/)
      .required()
      .error(new Error('email is invalid')),

    password: Joi.string().min(6).required(),
  };
  return Joi.validate(userDetails, schema);
}

export { validateUserSignUpDetails, validateUserSignInDetails };
